.MainReg{ 
    background: #FFF; 
  	border: 2px solid black;
  	box-sizing: border-box;
  	position: absolute;
  	height: 90vh;
  	width: 100%;
}
.TextBlockReg{
	color: #000;
	font-family: 'Calibri Light';
	font-size: 20pt;
	text-align: center;
	margin-top: 10pt;
	margin-bottom: 10pt;
}
.ImgCodeRegistration{
	display: block;
  	width: 90%;
  	height: 60vh;
  	margin-left: auto;
    margin-right: auto; 
}
.InputReg{
	margin-bottom: 20pt;
}
.ButtonReg{
	background-color: #6F389F;
	border-color: #6F389F;
	color: #FFF; 
	font-family: 'Century Gothic';
	font-size: 18pt;
}
.ButtonBlockReg{
	margin-right: 10vw;
	margin-left: 10vw;
	margin-bottom: 10pt;
}
.FontButtonReg{
	font-family: 'Century Gothic';
	font-size: 11pt;
	font-weight: bold;
	color: #FFF; 
	width: 100pt;
	height: 13pt;
	padding: 0px 0px 0px 0px;
	margin: 0px 0px 0px 0px;
}
